/* STYLE GUIDE STYLING*/

body {
  font-family: 'Open Sans', sans-serif;
  color: #333;
}

section {
  padding: 20px 10px;
}

section:nth-of-type(even) {
  background-color: #FAFAFA;
}

.content {
  margin: 0 auto;
  width: 900px;
}

/* PRIMARY COLORS */

button.green { background-color: #79BD9A; }
button.teal { background-color: #3B8686;  }
button.blue { background-color: #0B486B;  }

input[type="text"].green:focus { border-color: #79BD9A; }
input[type="text"].teal:focus { border-color: #3B8686; }
input[type="text"].blue:focus { border-color: #0B486B; }

/* HEADER */

header {
  box-sizing: border-box;
  width: 100%;
  height: 50px;
  background-color: #3B8686;
  color: white;
  line-height: 50px;
  padding: 0 20px;
}

.logo {
  display: inline-block;
  font-size: 1.5em;
}

nav {
  display: inline-block;
  float: right;
}

a {
  font-size: 0.9em;
  font-family: 'Montserrat', sans-serif;
  color: white;
  padding: 5px 10px;
  text-decoration: none;
  color: #F3F3F3;
}

a:hover {
  color: white;
}

.with-border {
  padding: 5px 15px;
  margin: 0 10px;
  border: 2px solid #F3F3F3;
  border-radius: 2px;
}

.with-border:hover {
  border: 2px solid white;
}

/* BUTTON */

button {
  font-family: 'Montserrat', sans-serif;
  margin: 0 10px;
  border: none;
  color: white;
  height: 45px;
  width: 200px;
  line-height: 45px;
  border-radius: 2px;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  position: relative;
  top: 0;
}

button:hover {
  top: -1px;
  box-shadow: 0 3px 3px rgba(0, 0, 0, 0.1);
}

button:active {
  top: 1px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
}

button:focus {
  outline: none;
  top: 1px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
}

/* INPUTS */

label {
  font-family: 'Montserrat', sans-serif;
  display: block;
  margin: 10px;
  font-size: 0.9em;
  color: #777;
}

input[type="text"] {
  box-sizing: border-box;
  border: 2px solid #DADADA;
  height: 45px;
  width: 400px;
  padding: 0 15px;
  border-radius: 2px;
}

input[type="text"]:focus {
  outline: none;
  border: 2px solid rgba(11, 72, 107, 0.5);
  transition-duration: 500ms;
}

